
import { Component, Vue } from 'vue-property-decorator';

interface DevicesModel {
  Id: string,
  Name: string,
  IsOn: boolean
}

@Component
export default class LightsData extends Vue {
  // domyslne zarowki
  devices: DevicesModel[] = [
    {
      Id: 'AIR011',
      Name: "Salon",
      IsOn: true
    },
    {
      Id: 'AIR012',
      Name: 'Sypialnia',
      IsOn: false
    },   
    {
      Id: 'WIN01',
      Name: "Taras",
      IsOn: true
    },
    {
      Id: 'WIN02',
      Name: 'Kuchnia',
      IsOn: false
    }
  ];
  display = false;
  newName = '';
  onDialogCancelClick() {
    this.display = false;
  }
  onDialogAddClick() {
    // jesli nowa nazwa zarowki jest '' to jej nie dodawaj
    if(this.newName.trim() == '') return;
    this.devices.push({
      Id: this.newName,
      Name: this.newName,
      IsOn: false
    });
    this.newName = '';
    this.display = false;
  }

  // po kliknieciu ustaw wartosc ktora pokaze dialog do dodania zarowki
  onAddClick() {
    this.display = true;
  }
}
